import React from 'react';
import { Link as NavLink } from 'react-router-dom';
import '../styles/navmenu.css'

function NavMenu(){
    return(
    <header className="navMenu">
        <NavLink exact to="/"><button className="navButton">HOME</button></NavLink>
        <NavLink exact to="/about"><button className="navButton">ABOUT</button></NavLink>
        <NavLink exact to="/projects"><button className="navButton">PROJECTS</button></NavLink>            
    </header>
    )
}

export default NavMenu;