import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import About from './pages/about';
import Projects from './pages/projects';
import App from './App';


function ReactRouterSetup(){
    return (
    <Router>
        <Route path="/">
            <App />            
        </Route>
        <Switch>
            <Route path="/about">
                <About />
            </Route>
            <Route path="/projects">
                <Projects />
            </Route>
        </Switch>
    </Router>
    )
}

export default ReactRouterSetup;